/* CommonCardStyles.css */
.card {
    width: 480px;
    height: 75px;
    border: 1px solid #D0D0D0;
    border-radius: 4px;
    background-color: #FFFFFF;
    padding: 10px;
    position: relative;
    margin: 5px 0;
  }
  
  .label {
    font-size: 14px;
    font-family: 'Times New Roman', serif;
    font-weight: normal;
    color: #000;
    position: absolute;
    top: 10px;
    left: 15px;
  }
  
  .value {
    font-size: 25px;
    font-family: 'Times New Roman', serif;
    font-weight: bold;
    color: #000;
    position: absolute;
    bottom: 10px;
    left: 15px;
  }
  
  .location {
    font-size: 20px;
    font-family: 'Times New Roman', serif;
    font-weight: bold;
    color: #000;
    position: absolute;
    bottom: 10px;
    left: 15px;
  }
  
  .distance {
    font-size: 12px;
    font-family: 'Times New Roman', serif;
    font-weight: normal;
    color: #666;
    position: absolute;
    top: 10px;
    right: 15px;
  }
  